@import "mixins";


body, h1, h2, h3, h4, h5 {
	margin: 0; }

body {
	background: #f5f5f5;
	max-width: 1900px;
	min-width: 1100px;
	// height: 100%
	margin: auto;
	// overflow: hidden
	font-family: 'Lato', sans-serif;
	// font-family: 'PT Sans', sans-serif
	// font-family: 'Open Sans Condensed', sans-serif
	// font-family: 'Helvetica-r'
	// font-family: 'Raleway', sans-serif;
	@include box-shadow(0px 0px 20px 1px darken($main-grey, 20%)); }

.max-w {
	max-width: 1406px;
	padding-left: 20px;
	padding-right: 20px;
	margin: auto; }


.block-h {
	font-weight: normal;
	text-align: center;
	font-size: 24px;
	width: 100%;
	color: $main-text; }
.hr {
	width: 129px;
	margin: 25px auto;
	height: 1px;
	background: $main-text;
	border: none; }
