@import "normalize";
@import "mixins";










.block-1 {
	width: 100%;
	// max-width: 1366px
	// height: 100%
	// height: 100vh
	// min-height: 1060px
	margin: 0 auto;
	background: url('/img/bl1-bg.png') no-repeat 50% 0;
	background-size: cover;
	padding-bottom: 100px;
	.head {
		padding-top: 20px;
		width: 100%;
		// text-align: center
		// position: absolute
		// left: 0
		// right: 0
		// top: 0
		margin: auto;
		@include flex;
		justify-content: space-between;
		box-sizing: border-box;
		div, a {
			display: inline-block; }
		.social {
			a {
				color: $main-blue;
				margin-right: 20px;
				text-decoration: none;
				@include transition-ease(0.5s); }
			a:hover {
				@include transform(scale(1.1)); }

			// a:before
			// 	position: absolute
			// 	width: 100%
			// 	height: 100%
			// 	background: $white
			// 	border-radius: 40px
			.social-icon {
				position: relative;
				width: 39px;
				height: 39px;
				border-radius: 40px;
				background: $white;
				display: table;
				text-align: center;
				vertical-align: middle;
				border: 2px solid $white;

				i {
					display: table-cell;
					vertical-align: middle;
					border: 2px solid $main-blue;
					border-radius: 40px; } } }

		.brand {
			text-align: center;
			.brand-orbit {
				width: 80px;
				height: 80px;
				background: url('/img/brand.png') 50% 50% no-repeat;
				text-align: center;
				a {
					position: relative;
					top: 50%;
					@include translate(0, -50%);
					color: $white;
					font-weight: bold;
					font-size: 12px;
					letter-spacing: 1px;
					text-decoration: none;
					@include transition-ease(0.5s); }
				:hover {
					color: $main-blue; } } }

		.numbers {
			text-align: right;
			.numb {
				display: block;
				color: $white;
				font-weight: bold;
				img {
					padding: 5px;
					margin-right: 3px;
					background: $white;
					border-radius: 20px;
					vertical-align: middle; } } } }

	.block-1-info {
		// position: relative
		// top: 50%
		// @include translate(0, -50%)
		@include flex;
		justify-content: space-between;
		margin-top: 200px;
		margin-bottom: 200px;
		box-sizing: border-box;
		.list {
			width: 50%;
			.info-h {
				margin: 0;
				color: $main-yellow;
				font-size: 43px;
				font-weight: bold;
				text-transform: uppercase; }
			h4 {
				font-size: 24px;
				color: $white;
				font-weight: lighter; }
			ul {
				list-style-type: none;
				color: $white;
				margin-top: 40px;
				padding: 0;
				li {
					// display: table
					margin-bottom: 15px;
					font-size: 14px;
					vertical-align: middle;
					img {
						margin-right: 10px;
						@include translate(0, 25%); }
					span {
						vertical-align: middle; } } } }
		.form:before {
			content: url(/img/girl-form.png);
			background: none;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			@include translate(0, -70%); } } }

.form {
	position: relative;
	background: $white;
	text-align: center;
	min-width: 359px;
	width: 40%;

	border-radius: 5px;
	padding-top: 80px;
	padding-bottom: 80px;
	// padding: 80px auto
	div {
		width: 75%;
		margin: auto;
		input {
			width: 100%;
			margin: 10px auto;
			border: 1px solid $grey;
			padding: 20px;
			box-sizing: border-box;
			font-size: 16px; } }
	.form-h {
		text-align: left;
		margin-bottom: 40px;
		span {
			text-decoration: underline; } }
	.button {
		background: $main-yellow;
		border: none;
		font-size: 20px;
		width: 75%;
		padding-top: 15px;
		padding-bottom: 15px; } }


