.block-8 {
	background: $white;
	padding-bottom: 120px;
	text-align: center;
	position: relative;
	overflow: hidden;
	iframe {
		border-radius: 5px;
		margin: auto; }

	.flags {
		position: absolute;
		width: 100%;
		top: 50%;

		.flag-right, .flag-left {
			background: $main-blue;
			height: 60px;
			width: 100px;
			display: inline-block;
			position: absolute; }
		.flag-left {
			left: 5%; }
		.flag-right {
			right: 5%; }
		.flag-left:after {
			content: '';
			display: inline-block;
			position: absolute;
			width: 0;
			height: 0;
			right: 0;
			border-top: 30px solid transparent;
			border-bottom: 30px solid transparent;
			border-right: 20px solid $white; }
		.flag-right:after {
			content: '';
			display: inline-block;
			position: absolute;
			width: 0;
			height: 0;
			left: 0;
			border-top: 30px solid transparent;
			border-bottom: 30px solid transparent;
			border-left: 20px solid $white; }

		.flag-left:before {
			content: '';
			width: 200%;
			height: 100%;
			background: $main-blue;
			position: absolute;
			top: -10px;
			left: -160%; }

		.flag-right:before {
			content: '';
			width: 200%;
			height: 100%;
			background: $main-blue;
			position: absolute;
			top: -10px;
			right: -160%; } } }


