.block-10 {
	position: relative;
	width: 100%;

	.pre-footer {
		// padding-top: 200px
		// padding-bottom: 100px
		background: $main-blue;
		position: absolute;
		z-index: 0;
		top: -300px;
		width: 100%;
		height: 540px;
		.max-w {
			position: relative;
			height: 100%; }
		.pre-footer-content {
			@include flex;
			justify-content: space-around;
			align-items: flex-end;
			width: 100%;
			position: absolute;
			bottom: 0;
			.links {
				width: 45%;
				padding-top: 75px;
				margin-bottom: 50px;
				.link:before {
					content: url(/img/loc.png);
					margin-right: 10px;
					position: relative;
					top: 5px; }
				.link {
					margin-bottom: 10px;
					text-decoration: underline;
					color: $white; } }
			.news {
				width: 15%;
				height: 540px;
				background: $white;
				// text-align: center
				@include flex;
				flex-direction: column;
				justify-content: flex-end;
				align-items: center;
				text-decoration: none;
				.news-text {
					color: $main-blue;
					font-weight: bold;
					display: inline-block;
					vertical-align: bottom;
					margin-bottom: 50px; }
				.blog {
					margin-bottom: 30px; } }
			.contacts {
				width: 30%;
				text-align: center;
				margin-bottom: 50px;
				div, a {
					display: inline-block; }
				.social {
					display: block;
					a {
