						margin-right: 20px;
						text-decoration: none;
						@include transition-ease(0.5s); }
					a:hover {
						@include transform(scale(1.1)); }
					.social-icon {
						position: relative;
						width: 39px;
						height: 39px;
						border-radius: 40px;
						background: $white;
